'use client';
import { motion } from 'framer-motion';
import { useState } from 'react';
import { useRouter } from 'next/router';

export default function SignUp() {
    const router = useRouter();
    const [formData, setFormData] = useState({
        username: '',
        email: '',
        password: '',
        place: '',
        age: '',
        gender: '',
        interested: '',
    });

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError(null);

        if (!formData.username || !formData.email || !formData.password || 
            !formData.place || !formData.age || !formData.gender || !formData.interested) {
            setError('All fields are required');
            return;
        }

        setLoading(true);

        try {
            const response = await fetch('http://localhost:5000/signup', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(formData),
            });

            const data = await response.json();

            if (response.ok) {
                router.push('/profile', { state: formData });
            } else {
                setError(data.message);
            }
        } catch {
            setError('An error occurred. Please try again later.');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen flex items-center justify-center bg-gradient-to-r from-purple-500 to-pink-500">
            <motion.div
                initial={{ opacity: 0, y: -50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
                className="bg-white p-8 rounded-lg shadow-2xl w-96"
            >
                <motion.h1
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    transition={{ delay: 0.3 }}
                    className="text-3xl font-bold text-center mb-6 text-purple-600"
                >
                    Sign Up
                </motion.h1>

                {error && <div className="text-red-500 text-center mb-4">{error}</div>}

                <form onSubmit={handleSubmit}>
                    {['username', 'email', 'password', 'place', 'age'].map((field, index) => (
                        <motion.div
                            key={field}
                            initial={{ x: -100, opacity: 0 }}
                            animate={{ x: 0, opacity: 1 }}
                            transition={{ delay: 0.4 + index * 0.1 }}
                            className="mb-4"
                        >
                            <input
                                type={field === 'email' ? 'email' : field === 'password' ? 'password' : 'text'}
                                placeholder={field.charAt(0).toUpperCase() + field.slice(1)}
                                className="w-full px-4 py-2 rounded-lg border border-purple-300 focus:outline-none focus:ring-2 focus:ring-purple-500"
                                onChange={(e) => setFormData({ ...formData, [field]: e.target.value })}
                                value={formData[field]}
                            />
                        </motion.div>
                    ))}

                    {['gender', 'interested'].map((field, index) => (
                        <motion.div
                            key={field}
                            initial={{ x: -100, opacity: 0 }}
                            animate={{ x: 0, opacity: 1 }}
                            transition={{ delay: 0.9 + index * 0.1 }}
                            className="mb-4"
                        >
                            <select
                                className="w-full px-4 py-2 rounded-lg border border-purple-300 focus:outline-none focus:ring-2 focus:ring-purple-500"
                                onChange={(e) => setFormData({ ...formData, [field]: e.target.value })}
                                value={formData[field]}
                            >
                                <option value="">Select {field.charAt(0).toUpperCase() + field.slice(1)}</option>
                                {field === 'gender' ? (
                                    <>
                                        <option value="male">Male</option>
                                        <option value="female">Female</option>
                                        <option value="other">Other</option>
                                    </>
                                ) : (
                                    <>
                                        <option value="men">Men</option>
                                        <option value="women">Women</option>
                                        <option value="both">Both</option>
                                    </>
                                )}
                            </select>
                        </motion.div>
                    ))}

                    <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        className="w-full bg-gradient-to-r from-purple-500 to-pink-500 text-white py-2 rounded-lg font-semibold hover:opacity-90 transition-all"
                        type="submit"
                        disabled={loading}
                    >
                        {loading ? 'Creating Account...' : 'Create Account'}
                    </motion.button>
                </form>
            </motion.div>
        </div>
    );
}